html {
  font-size: 24px;
  line-height: 2;
}

header {
  width: 15%;
  height: 100vh;
  background-color: black;

  display: flex;
  align-items: center;
  justify-content: center;

  nav {
    text-align: center;

    a {
      color: white;
      display: block;
      font-size: .8rem;
    }
  }
}

main {
  width: 80%;
  height: 100vh;
  overflow: auto;
}

.wrap {
  display: flex;
  justify-content: space-between;
}